"use client"

import { useState, useEffect, useCallback } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2
} from "lucide-react"
import logo from "@/lib/images/image.png"

// --- 인터페이스 ---
interface Promotion {
  id: string; 
  name: string;         
  pointAmount: number; 
  totalStock: number;  
  startTime: string;   
  endTime: string;     
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
  etc: string | null;
  createdAt: string; 
}

interface PromotionStatus {
  queueSize: number;
  participantCount: number;
  winnerCount: number;
  totalStock: number;
  remainingStock: number;
}

interface AdvisorRequest {
  userId: string;
  userName: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  const BASE_URL = "http://34.50.7.8:3000"
  const PROMO_API = `${BASE_URL}/v1/promotions`
  const ADMIN_USER_API = `${BASE_URL}/v1/user` 

  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)
  const [statusData, setStatusData] = useState<PromotionStatus | null>(null)
  const [isStatusOpen, setIsStatusOpen] = useState(false)
  const [targetPromoName, setTargetPromoName] = useState("")
  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)
  const itemsPerPage = 5 

  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=${itemsPerPage}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content); 
        setTotalPromoPages(data.result.totalPages);
      }
    } catch (error) { console.error("로드 실패", error); } finally { setIsLoading(false); }
  }, [promoPage])

  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/pending`, {
        method: "GET",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) { setAdvisorRequests(data.result); }
    } catch (error) { console.error("심사 로드 실패", error); }
  }, [])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    if (role !== "MANAGER" && role !== "MASTER") { router.push("/login"); return; }
    setUserRole(role); setUserName(name); setIsMounted(true);
    fetchPromotions(); fetchPendingAdvisors();
  }, [router, fetchPromotions, fetchPendingAdvisors])

  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const action = currentStatus === "SCHEDULED" ? "start" : "end"
    if (!confirm(`프로모션을 ${action === "start" ? "시작" : "종료"}하시겠습니까?`)) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, {
        method: "PATCH",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
    } catch (error) { alert("상태 변경 실패"); }
  }

  const fetchPromotionStatus = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${PROMO_API}/${id}/status`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setStatusData(data.result); 
        setTargetPromoName(name); 
        setIsStatusOpen(true);
      }
    } catch (error) { console.log("서버 통신 오류"); }
  }

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-[#F8F9FC] text-slate-900 font-sans">
      <header className="border-b bg-white/80 backdrop-blur-md sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src={logo} alt="Logo" width={90} height={25} className="h-6 w-auto" priority />
            <Badge className="bg-primary/10 text-primary border-none text-[10px] font-bold">ADMIN</Badge>
          </Link>
          <Button variant="ghost" size="icon" onClick={() => {localStorage.clear(); router.push("/login");}} className="rounded-full">
            <LogOut className="h-5 w-5 text-slate-500" />
          </Button>
        </div>
      </header>

      <main className="container mx-auto px-4 py-6">
        {/* 상단 섹션 */}
        <div className="mb-8">
          <h1 className="text-2xl font-black tracking-tight italic uppercase">Dashboard</h1>
          <p className="text-slate-500 text-sm font-medium">안녕하세요, {userName} 관리자님.</p>
        </div>

        <div className="grid lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2 space-y-6">
            <Tabs defaultValue="promotions" className="w-full">
              <TabsList className="grid w-full grid-cols-2 mb-4 bg-slate-200/50 p-1 rounded-xl">
                <TabsTrigger value="promotions" className="rounded-lg font-bold py-2">이벤트 관리</TabsTrigger>
                <TabsTrigger value="advisors" className="rounded-lg font-bold py-2">전문가 승인</TabsTrigger>
              </TabsList>

              <TabsContent value="promotions">
                <Card className="border-none shadow-sm rounded-2xl overflow-hidden bg-white">
                  <div className="p-5 border-b flex justify-between items-center">
                    <h2 className="font-bold flex items-center gap-2"><Sparkles className="h-4 w-4 text-primary" /> 진행 현황</h2>
                    <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-lg h-8 text-xs font-bold">
                      <Plus className="h-3.5 w-3.5 mr-1" /> 등록
                    </Button>
                  </div>

                  <div className="p-0 sm:p-4">
                    {isLoading ? (
                      <div className="py-20 flex justify-center"><Loader2 className="animate-spin" /></div>
                    ) : (
                      <div className="divide-y sm:divide-y-0">
                        {/* 모바일: 카드 레이아웃 / 데스크톱: 숨김 */}
                        <div className="block sm:hidden">
                          {promotions.map((p) => (
                            <div key={p.id} className="p-4 space-y-4">
                              <div className="flex justify-between items-start">
                                <div>
                                  <h3 className="font-black text-slate-900 italic uppercase">{p.name}</h3>
                                  <p className="text-[11px] font-bold text-primary">{p.pointAmount.toLocaleString()}P · {p.totalStock}명</p>
                                </div>
                                <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="text-[10px] font-black">{p.status}</Badge>
                              </div>
                              
                              <div className="bg-slate-50 p-3 rounded-lg space-y-1">
                                <div className="flex justify-between text-[10px] font-bold text-slate-500">
                                  <span>시작: {p.startTime.replace('T', ' ').slice(5, 16)}</span>
                                  <span>종료: {p.endTime.replace('T', ' ').slice(5, 16)}</span>
                                </div>
                              </div>

                              <div className="flex gap-2">
                                {p.status === "SCHEDULED" && (
                                  <Button className="flex-1 bg-green-600 hover:bg-green-700 h-10 rounded-xl font-bold text-xs" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}>
                                    <Play className="h-3.5 w-3.5 mr-1 fill-current" /> 이벤트 시작
                                  </Button>
                                )}
                                {p.status === "ACTIVE" && (
                                  <Button variant="destructive" className="flex-1 h-10 rounded-xl font-bold text-xs" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}>
                                    <Square className="h-3.5 w-3.5 mr-1 fill-current" /> 강제 종료
                                  </Button>
                                )}
                                <Button variant="outline" className="w-12 h-10 rounded-xl" onClick={() => fetchPromotionStatus(p.id, p.name)}>
                                  <Info className="h-4 w-4" />
                                </Button>
                              </div>
                            </div>
                          ))}
                        </div>

                        {/* 데스크톱: 기존 테이블 레이아웃 */}
                        <div className="hidden sm:block overflow-x-auto">
                          <table className="w-full text-sm">
                            <thead className="bg-slate-50 text-slate-500 text-[10px] uppercase font-black">
                              <tr>
                                <th className="px-4 py-3 text-left">이벤트 정보</th>
                                <th className="px-4 py-3 text-center">상태</th>
                                <th className="px-4 py-3 text-right">관리</th>
                              </tr>
                            </thead>
                            <tbody className="divide-y">
                              {promotions.map((p) => (
                                <tr key={p.id} className="hover:bg-slate-50/50">
                                  <td className="px-4 py-4">
                                    <p className="font-bold uppercase italic">{p.name}</p>
                                    <p className="text-xs text-slate-400 font-mono">{p.startTime.slice(5, 16)} ~</p>
                                  </td>
                                  <td className="px-4 py-4 text-center">
                                    <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"}>{p.status}</Badge>
                                  </td>
                                  <td className="px-4 py-4 text-right">
                                    <div className="flex justify-end gap-2">
                                      {p.status === "SCHEDULED" && <Button size="sm" className="bg-green-600" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}><Play className="h-3.5 w-3.5 fill-current" /></Button>}
                                      {p.status === "ACTIVE" && <Button size="sm" variant="destructive" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}><Square className="h-3.5 w-3.5 fill-current" /></Button>}
                                      <Button size="sm" variant="outline" onClick={() => fetchPromotionStatus(p.id, p.name)}><Info className="h-4 w-4" /></Button>
                                    </div>
                                  </td>
                                </tr>
                              ))}
                            </tbody>
                          </table>
                        </div>
                      </div>
                    )}
                  </div>
                  
                  {/* 페이지네이션 */}
                  <div className="p-4 border-t flex justify-center items-center gap-4">
                    <Button variant="ghost" size="sm" onClick={() => setPromoPage(p => Math.max(0, p-1))} disabled={promoPage === 0}><ChevronLeft className="h-4 w-4" /></Button>
                    <span className="text-xs font-black italic">{promoPage + 1} / {totalPromoPages}</span>
                    <Button variant="ghost" size="sm" onClick={() => setPromoPage(p => Math.min(totalPromoPages - 1, p+1))} disabled={promoPage >= totalPromoPages - 1}><ChevronRight className="h-4 w-4" /></Button>
                  </div>
                </Card>
              </TabsContent>
            </Tabs>
          </div>

          {/* 사이드바 - 운영 안내 */}
          <div className="space-y-6">
             <Card className="p-6 border-none shadow-sm rounded-2xl bg-white">
                <h2 className="font-black italic uppercase mb-4 flex items-center gap-2"><ShieldCheck className="h-5 w-5 text-primary" /> 가이드</h2>
                <div className="space-y-4">
                  <div className="p-3 bg-slate-50 rounded-xl border border-slate-100">
                    <p className="text-xs font-black text-primary uppercase mb-1">Status Check</p>
                    <p className="text-[11px] font-medium leading-relaxed text-slate-600">아이콘(i)을 누르면 실시간 대기열과 남은 수량을 확인하실 수 있습니다.</p>
                  </div>
                  <div className="p-3 bg-slate-50 rounded-xl border border-slate-100">
                    <p className="text-xs font-black text-slate-400 uppercase mb-1">Warning</p>
                    <p className="text-[11px] font-medium leading-relaxed text-slate-600">이벤트를 강제 종료하면 즉시 당첨 프로세스가 중단되며 복구할 수 없습니다.</p>
                  </div>
                </div>
             </Card>
          </div>
        </div>
      </main>

      {/* 실시간 현황 모달 */}
      <Dialog open={isStatusOpen} onOpenChange={setIsStatusOpen}>
        <DialogContent className="w-[90vw] max-w-[400px] rounded-3xl p-6 border-none">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-center">Live Monitor</DialogTitle></DialogHeader>
          {statusData && (
            <div className="grid grid-cols-2 gap-3 my-4">
              <div className="bg-slate-100 p-4 rounded-2xl text-center">
                <p className="text-[10px] font-black text-slate-400 uppercase">Queue</p>
                <p className="text-xl font-black">{statusData.queueSize.toLocaleString()}</p>
              </div>
              <div className="bg-slate-100 p-4 rounded-2xl text-center">
                <p className="text-[10px] font-black text-slate-400 uppercase">Winners</p>
                <p className="text-xl font-black">{statusData.winnerCount.toLocaleString()}</p>
              </div>
              <div className="col-span-2 bg-primary/10 p-6 rounded-2xl text-center border border-primary/20">
                <p className="text-[10px] font-black text-primary uppercase">Remaining Stock</p>
                <p className="text-4xl font-black text-primary">{statusData.remainingStock.toLocaleString()}</p>
              </div>
            </div>
          )}
          <Button onClick={() => setIsStatusOpen(false)} className="w-full h-12 rounded-2xl font-black uppercase italic">Close</Button>
        </DialogContent>
      </Dialog>
    </div>
  )
}